.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.console {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: stretch;
  text-align: left;
}

.historyPaneWrap {
  flex: 0 0 16%;
}

.history-title-bar {
  display: none;
}

.graphiql-container {
  flex: 1 1 auto;
}
